<template>
    <div class="appointment">
        <div class="bg" :style="{backgroundImage:'url('+require('../../assets/service02.png')+')'}"></div>
        <!-- <div class="bg" style="background: url('../../assets/service02.png') no-repeat;"></div> -->
        <van-notice-bar mode="closeable" color="rgb(230,0,18)">
            傍晚用餐人数较多，建议您提前2小时在线预约，过号延3桌哦~ 祝您用餐愉快！
        </van-notice-bar>
        <div class="content">
            <div class="time">营业时间：早晨9:00-次日凌晨3:00</div>
            <div><van-icon name="location" />天津市南开区二马路225号增1号天海火锅总店</div>
            <div class="dengdai">
                <div class="title">前方正在等待</div>
                <van-row>
                    <van-col span="8">
                        <div class="box">
                            <div class="name">小桌</div>
                            <div class="people">(1-4人)</div>
                            <div class="num">56桌</div>
                            <van-button type="default" @click="getNum('小桌',57)">取号</van-button>
                        </div>
                    </van-col>
                    <van-col span="8">
                        <div class="box">
                            <div class="name">中桌</div>
                            <div class="people">(4-6人)</div>
                            <div class="num">39桌</div>
                            <van-button type="default" @click="getNum('中桌',40)">取号</van-button>
                        </div>
                    </van-col>
                    <van-col span="8">
                        <div class="box">
                            <div class="name">大桌</div>
                            <div class="people">(6-10人)</div>
                            <div class="num">20桌</div>
                            <van-button type="default" @click="getNum('大桌',21)">取号</van-button>
                        </div>
                    </van-col>
                </van-row>
            </div>
        </div>
        
        <footer-menu></footer-menu>
    </div>
</template>
<script>
import footerMenu from '@/components/foot/foot';
export default {
    components:{
        footerMenu
    },
    data(){
        return {
        }
    },
    methods: {
        getNum (num,id) {
            this.$dialog.alert({
                title: '取号成功',
                message: '您'+ num + '取号成功，号码为' + id + ', 过号延后3桌'
                }).then(() => {
                // on close
            });
        }
    }
}
</script>
<style lang="less" scoped>
.bg{
    background-size:100% 200px;
    height: 200px;
    width: 100% 
}
.content{
    padding: 55px 18px;
}
.time{
    color: #000;
    margin-bottom: 13px;
    font-size: 16px;
}
.dengdai{
    border-top: 1px solid #dedede;
    margin-top: 15px;
}
.title{
    margin-top: 36px;
    font-size: 16px;
    color: #000;
    font-weight: 600;
}
.box{
    text-align: center;
    margin-top: 22px;
}
.name{
    font-size: 18px;
    color: #000;
    margin-bottom: 8px;
}
.num{
    font-size: 18px;
    color: rgb(203, 0, 18);
    margin-top: 3px;
    margin-bottom: 10px;
}
</style>




