<template>
  <div id="app">
    <transition name="router-fade" mode="out-in">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import Vue from 'vue';
import Vant from 'vant';
Vue.use(Vant);
export default {
  name: 'App'
}
</script>

<style lang="less">
@import url('vant/lib/index.css');
.text-center{
    text-align: center;
}
#app {
  font-family: 'Microsoft YaHei';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
  margin: auto;
}
.router-fade-enter-active, .router-fade-leave-active {
	  	transition: opacity .3s;
	}
.router-fade-enter, .router-fade-leave-active {
    opacity: 0;
}
a:focus,a:hover{
  text-decoration: none;
}
    .nav-row{
        height: 46px;
        position: fixed;
        top: 0;
        width: 100%;
        background: rgb(230,0,18);
        z-index: 100;
        line-height: 46px;
        color: #fff;
        .nav-content{
            position: relative;
        }
        .nav-let-bar{
            position: absolute;
            left: 0;
            margin-left: 10px;
            width: 60px;
            height: 46px;
        }
        .nav-title{
            max-width: 60%;
            margin: auto;
            .text-center;
            font-size: 14px;
        }
        .photo-icon{
            margin-top: 3px;
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }
</style>



