<template>
    <div>
        <div class="nav">
            <van-tabbar v-model="active" active-color="rgb(230,0,18)">
            <van-tabbar-item icon="shop" to="/home"> 主页 </van-tabbar-item>
            <van-tabbar-item icon="cart" dot to="/table">点餐</van-tabbar-item>
            <van-tabbar-item icon="bell" to="/appointment">预约</van-tabbar-item>
            <van-tabbar-item icon="manager" to="/my">我的</van-tabbar-item>
            </van-tabbar>
        </div>
    </div>
</template>
<script>
export default {
    name: 'nav',
    data() {
        return {
            active: 0
        }
    },
    watch : {
        $route: {
            handler: function(val, oldVal){
                // console.log(val);
            },
            // 深度观察监听
            deep: true
        }
    }
}
</script>
<style lang="less">
    .left-nav{
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 999;
        background-color: red;
    }
</style>





