<template>
    <div class="appointment">
        <nav class="nav-bar">
            <van-nav-bar
            title="支付"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            />
        </nav>
        <!-- 优惠券单元格 -->
        <van-coupon-cell style="margin-top:55px"
        :coupons="coupons"
        :chosen-coupon="chosenCoupon"
        @click="showList = true"
        />

        <!-- 优惠券列表 -->
        <van-popup v-model="showList" position="bottom">
        <van-coupon-list
            :coupons="coupons"
            :chosen-coupon="chosenCoupon"
            :disabled-coupons="disabledCoupons"
            @change="onChange"
            @exchange="onExchange"
        />
        </van-popup>
        <div class="pay_num">
            {{id}}元
        </div>
        <div class="pay_">
                <van-button type="primary" @click="pay">微信支付</van-button>
                <van-button type="info" @click="pay">支付宝支付</van-button>
                <van-button type="danger" @click="pay">招行手机银行</van-button>
        </div>
        <footer-menu></footer-menu>
    </div>
</template>
<script>
import footerMenu from '@/components/foot/foot';
    const coupon = {
        available: 1,
        condition: '无使用门槛\n最多优惠12元',
        reason: '',
        value: 150,
        name: '优惠券名称',
        startAt: 1552806679,
        endAt: 1560755479,
        valueDesc: '1.5',
        unitDesc: '元'
    };
    const coupon2 = {
        available: 1,
        condition: '满600可减80元',
        reason: '',
        value: 150,
        name: '优惠券名称',
        startAt: 1552806679,
        endAt: 1555485079,
        valueDesc: '80',
        unitDesc: '元'
    };
export default {
    components:{
        footerMenu
    },
    data(){
        return {
            chosenCoupon: -1,
            coupons: [coupon],
            disabledCoupons: [coupon2],
            showList : false,
            id : 0
        }
    },
    created () {
        this.id = this.$route.params.id
    },
    methods: {
        //返回
        onClickLeft() {
            let that = this;
            that.$router.push("/foodList");
        },
         onChange(index) {
            this.showList = false;
            this.chosenCoupon = index;
        },
        onExchange(code) {
            this.coupons.push(coupon);
        },
        pay () {
            this.$notify({
                message: '支付成功',
                duration: 1000,
                background: '#1989fa'
            });           
        }
    }
}
</script>
<style lang="less" scoped>
.pay_{
    margin-top: 100px;
    text-align: center;
}
.pay_num{
    margin-top: 50px;
    font-size: 30px;
    text-align: center;
}
</style>




